// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";
import router from "./router";
import store from "./store";
import { MintUI, Lazyload, InfiniteScroll } from "mint-ui";
import "mint-ui/lib/style.css";
import VueCookie from "vue-cookie";
import vuePicturePreview from "vue-picture-preview";
import BusPlugin from "./assets/js/bus";
import { getopenid } from "./assets/js/wechat";
getopenid();
Vue.use(vuePicturePreview);
// import axios from 'axios';
Vue.use(VueCookie);
Vue.config.productionTip = false;
Vue.use(Lazyload);
Vue.use(InfiniteScroll);
Vue.use(BusPlugin);


const history = window.sessionStorage
history.clear()
let historyCount = history.getItem('count') * 1 || 0
history.setItem('/', 0)

let isPush = false
let endTime = Date.now()
let methods = ['push', 'go', 'replace', 'forward', 'back']

document.addEventListener('touchend', () => {
  endTime = Date.now()
})
methods.forEach(key => {
  let method = router[key].bind(router)
  router[key] = function (...args) {
    isPush = true
    method.apply(null, args)
  }
})

store.registerModule('vux', {
  state: {
    demoScrollTop: 0,
    isLoading: false,
    direction: 'forward'
  },
  mutations: {
    updateDemoPosition(state, payload) {
      state.demoScrollTop = payload.top
    },
    updateLoadingStatus(state, payload) {
      state.isLoading = payload.isLoading
    },
    updateDirection(state, payload) {
      state.direction = payload.direction
    }
  },
  actions: {
    updateDemoPosition({
      commit
    }, top) {
      commit({
        type: 'updateDemoPosition',
        top: top
      })
    }
  }
})
/* eslint-disable no-new */
router.beforeEach((to, from, next) => {
  store.commit('updateLoadingStatus', {
    isLoading: true
  })

  const toIndex = history.getItem(to.path)
  const fromIndex = history.getItem(from.path)

  if (toIndex) {
    if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
      store.commit('updateDirection', {
        direction: 'forward'
      })
    } else {
      // 判断是否是ios左滑返回
      if (!isPush && (Date.now() - endTime) < 377) {
        store.commit('updateDirection', {
          direction: ''
        })
      } else {
        store.commit('updateDirection', {
          direction: 'reverse'
        })
      }
    }
  } else {
    ++historyCount
    history.setItem('count', historyCount)
    to.path !== '/' && history.setItem(to.path, historyCount)
    store.commit('updateDirection', {
      direction: 'forward'
    })
  }

  if (/\/http/.test(to.path)) {
    let url = to.path.split('http')[1]
    window.location.href = `http${url}`
  }
  // store.commit("updateLoadingStatus", { isLoading: true });
  // console.log("是否前进1",router.isBack);
  switch (to.path) {
    case "/f":
    case "/pay":
    case "/list":
    case "/od":
      console.log("需要登陆");
      let name = VueCookie.get("gdmobileusername");
      let phone = VueCookie.get("gdmobileuserphone");
      let serc = VueCookie.get("usersecret");
      if (
        name == "" ||
        name == null ||
        phone == "" ||
        phone == null ||
        serc == "" ||
        serc == null
      ) {
        router.push("/login");
        MintUI.Toast("请先登录");
      } else {
        console.log("已经登陆");
        next();
      }
      break;
    default:
      console.log("不需要登陆");
      next();
      break;
  }
});

router.afterEach(function (to) {
  isPush = false
  store.commit('updateLoadingStatus', {
    isLoading: false
  })
  
})
// router.afterEach((to) => {
//   // console.log("是否前进",store.state.isback);

//   // store.commit("updateLoadingStatus", { isLoading: false });
//   // // next();
// });
new Vue({
  el: "#app",
  router,
  store,
  components: { App },
  template: "<App/>"
});



