<template>
  <div id="app">
    <!-- {{isLoading}} -->
    <transition @after-enter="$vux.bus && $vux.bus.$emit('vux:after-view-enter')" :name="viewTransition" :css="!!direction">
      <router-view class="router-view"></router-view>
    </transition>
    <lg-preview></lg-preview>

  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: "App",
  computed: {},
  mounted() {
    if (location.hostname == "m.wandaly.net") {
      this.$cookie.set("WHERE", "12", 7);
    } else {
      this.$cookie.set("WHERE", "11", 7);
    }
    // this.getLocation();
  },
  computed: {
    viewTransition() {
      if (!this.direction) return "";
      console.log("vux-pop-" + (this.direction === "forward" ? "in" : "out"));
      return "vux-pop-" + (this.direction === "forward" ? "in" : "out");
    },
    ...mapState({
      route: state => state.route,
      path: state => state.route.path,
      deviceready: state => state.app.deviceready,
      demoTop: state => state.vux.demoScrollTop,
      isLoading: state => state.vux.isLoading,
      direction: state => state.vux.direction
    })
  },
  methods: {
    getLocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition((success, error) => {
          console.log("定位成功", success);

          console.log("定位失败", error);
        });
      } else {
        console.log("不支持");
      }
    }
  }
};
</script>

<style>
image[lazy="loading"] {
  width: 100%;
  height: 100%;
  margin: auto;
}
img {
  width: 100%;
  height: 100%;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
p {
  margin: 0;
  padding: 0;
}
html,
body {
  height: 100%;
  margin: 0;
  background-color: #f1f1f1;
}
.flex-common {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 60px; */
  height: 100%;
  background-color: #f1f1f1;
}
.router-view {
  width: 100%;
}
.slide-fade-enter-active {
  transition: all 0.5s ease;
}
.slide-fade-leave-active {
  transition: all 0.5s ease;
}
.slide-fade-enter,
.slide-fade-leave-to {
  opacity: 0;
  /* transform: translateX(-100%); */
}

.vux-pop-out-enter-active,
.vux-pop-out-leave-active,
.vux-pop-in-enter-active,
.vux-pop-in-leave-active {
  will-change: transform;
  transition: all 500ms;
  height: 100%;
  top: 0px;
  position: absolute;
  backface-visibility: hidden;
  perspective: 1000;
}
.vux-pop-out-enter {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
.vux-pop-out-leave-active {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.vux-pop-in-enter {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.vux-pop-in-leave-active {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
</style>



